.size-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: dense;
  column-gap: 7px;
  row-gap: 10px;
}
input[type="checkbox"]:checked + .size-list__marker {
  border: 2px solid #63d1bb;
}
.size-list__marker {
  display: flex;
  flex-wrap: wrap;
  width: 82px;
  min-height: 52px;
